.navMenu {
  --tw-border-opacity: 1;
    border-bottom-color: rgb(219 219 219/var(--tw-border-opacity));
    border-bottom-width: 1px;
    border-bottom-style: ridge;
    display: flex;
    flex-direction: column;
    gap: 25px;
    margin: 0 13px;
    padding: 30px 0;
}
  
  .menuItem {
    --tw-text-opacity: 1;
    color: rgb(120 116 134/var(--tw-text-opacity));
    display: flex;
    font-size: 1rem;
    font-weight: 500;
    gap: 14px;
    line-height: 1.5rem;  
  }
  .menuItem span {
    display: none;
  }
  
  @media only screen and (min-width: 640px){
    .menuItem{
      margin-left: 9px;
    } 
    .menuItem span{
      display: block;
    }
  }
  